import GoogleLogin from  "@stack-pulse/next-google-login"

import Cookies from "js-cookie";
import { Jwt } from "jsonwebtoken";

function Login() {

    function onLogin(googleResponse){
        console.log(googleResponse);
        const token = Jwt.sign({email:googleResponse.profileObj.email}, process.env.REACT_APP_JWT)
        Cookies.set("access_token",token, { expires: 15/86400 });
    }
    function getStringClientID(){
      return process.env.REACT_APP_GOOGLE_CLIENTID;
    }
  return (
    <div className="GoogleLogin">
        <GoogleLogin
        clientId={getStringClientID()}
        buttonText="Continue with Google"
        onSuccess={onLogin}
        onFailure={console.log}
        cookiePolicy={'single_host_origin'}
        />
        
    </div>
  );
}

export default Login;
